import Routing from '../../../vendor/friendsofsymfony/jsrouting-bundle/Resources/public/js/router.min';
const routes = require('./fos_js_routes.json');
Routing.setRoutingData(routes);

/*
|--------------------------------------------------------------------------
| Global File constance
|--------------------------------------------------------------------------
*/

// form identify
let companyForm = document.getElementById('company-steps-form');
let departmentForm = document.getElementById('department-steps-form');
let shareholderForm = document.getElementById('shareholer-steps-form');
let positioningForm = document.getElementById('positioning-steps-form');


// form url binding
let urlCompany = Routing.generate('start_member');
let urlShareholder = Routing.generate('second_member');
let urlDepartment = Routing.generate('third_member');
let urlPositioning = Routing.generate('fourth_member');


// components
let button = document.getElementById('steps-formBtn');
let loader = document.getElementById('loader');
let text = document.getElementsByTagName("textarea");

let textArray = [];
for (let i=0; i < text.length; i++) {
    textArray.push(text[i].id);
}

if (textArray.length > 0) {
    textArray.map((elt) => {
        expandTextarea(elt);
    });
}

/*
|--------------------------------------------------------------------------
| Ajax Calling Function
|--------------------------------------------------------------------------
*/

if (companyForm) {
    companyForm.addEventListener('submit', function (event) {
        event.preventDefault();
        AjaxCallPostMethod(companyForm, urlCompany)
            .then((data) => {
                StateLoader(loader, false);
                processData(data, companyForm, "form-fse-steps", true);
            })
            .catch((error) => {
                console.error(error);
            })
    });
}

if (departmentForm) {
    departmentForm.addEventListener('submit', function (event) {
        event.preventDefault();
        AjaxCallPostMethod(departmentForm, urlDepartment)
            .then((data) => {
                StateLoader(loader, false);
                processData(data, departmentForm, "form-fse-steps", false);
            })
            .catch((error) => {
                console.error(error);
            })
    });
}

if (shareholderForm) {
    shareholderForm.addEventListener('submit', function (event) {
        event.preventDefault();
        AjaxCallPostMethod(shareholderForm, urlShareholder)
            .then((data) => {
                StateLoader(loader, false);
                processData(data, shareholderForm, "form-fse-steps", false);
            })
            .catch((error) => {
                console.error(error);
            })
    });
}

if (positioningForm) {
    positioningForm.addEventListener('submit', function (event) {
        event.preventDefault();
        AjaxCallPostMethod(positioningForm, urlPositioning)
            .then((data) => {
                StateLoader(loader, false);
                processData(data, positioningForm, "form-fse-steps", false);
            })
            .catch((error) => {
                console.error(error);
            })
    });
}


/*
|--------------------------------------------------------------------------
| Function
|--------------------------------------------------------------------------
*/


/**
 * Ajax calling For Post Method
 * @param formElement
 * @param FormElementUrl
 * @returns {Promise<any>}
 * @constructor
 */
const AjaxCallPostMethod = (formElement, FormElementUrl) => {
    return new Promise(function (resolve, reject) {
        let xhr = new XMLHttpRequest();
        let formData = new FormData(formElement);
        xhr.addEventListener('load', function()
        {
            if (this.readyState === 4 ) {
                if (this.status === 200) {
                    StateLoader(loader, false);
                    resolve(JSON.parse(this.response))
                } else {
                    StateLoader(loader, false);
                    reject(this.status)
                }
            }
        });
        xhr.open("POST", FormElementUrl, true);
        xhr.setRequestHeader('X-Requested-With', 'XMLHttpRequest');
        xhr.send(formData);
    })
};


/**
 * Loader process
 * @param element
 * @param state
 * @constructor
 */
const StateLoader = (element, state) => {
    if (state === true) {
        element.classList.remove("form-loader-hidden");
        element.style.display = "block";
    }else {
        element.classList.add("form-loader-hidden");
        element.style.display = "none";
        enableBtn(button);
    }
};

/**
 * Button Enable
 * @param elementBtn
 * @returns {boolean}
 */
const enableBtn = elementBtn => elementBtn.disabled = false;

/**
 * Data response process
 * @param data
 * @param elem
 * @param wrapper
 * @param redirect
 */
const processData = (data, elem, wrapper = null, redirect = true) => {
    if (data.status === 200) {
        appAlert(data.info, 'success', data.title, 3000);
        elem.reset();
        if (redirect && redirect === true) {
            if (wrapper) document.getElementById(wrapper).style.display = "none";
            // Preload is class Loader for Parent Layout
            document.getElementById('preloader').style.display = "block";
            window.location.href = data.response.url;
        }
    } else {
        appAlert(data.info, 'error', data.title, 3000);
    }
};

const appAlert = ($message, $type, $title, $duration) => {
    app.toast($message, {
        actionColor: $type,
        actionTitle: $title,
        duration: $duration
    });
    // here declare let el after element create but before doesn't not exist
   let el = document.querySelector('.toast');
   if (el.hasAttribute("style")) {
       el.style.display = 'flex';
   }
    setTimeout(function(){
        el.style.display = 'none';
    }, $duration);
};


const expandTextarea = (id) => {
    document.getElementById(id).addEventListener('keyup', function() {
        this.style.overflow = 'hidden';
        this.style.height = 0;
        this.style.height = this.scrollHeight + 'px';
    }, false);
};


