require('./jquery-message-validate');

// initial variable
// form
const companyForm = $('#company-steps-form');
const departmentForm = $('#department-steps-form');
const shareholderForm = $('#shareholer-steps-form');
const positioningForm = $('#positioning-steps-form');

//Error
let errorClass = 'is-invalid';
let validClass = 'success';

// components
let loader = document.getElementById('loader');
let button = document.getElementById('steps-formBtn');

// array.map class form sending ajax in app-bundle.js
let container = [companyForm, departmentForm, shareholderForm, positioningForm];


$(document).ready(function () {
    // init default setting validator
    $.validator.setDefaults({
        wrapper: 'div',
        errorClass:'invalid-feedback',
        highlight: function(element) {
            $(element).addClass(errorClass).removeClass(validClass);
            $(element.form).find("label[for=" + element.id + "]").addClass(errorClass);
        },
        unhighlight: function(element) {
            $(element).removeClass(errorClass).addClass(validClass);
            $(element.form).find("label[for=" + element.id + "]").removeClass(errorClass);
        }
    });
    // init validator with form
    container.map((elt) => {
        elt.validate({
            onkeyup: false,
            submitHandler: function (form) {
                StateLoader(loader, button);
            }
        });
    });

});

const StateLoader = (element, elementBtn) => {
    element.classList.remove("form-loader-hidden");
    element.style.display = "block";
    disableBtn(elementBtn);
};

const disableBtn = elementBtn => elementBtn.disabled = true;


